import React from 'react';
import { withRouter } from 'react-router-dom';
import DashboardNavbar from '../../components/Navbar/dashboardNavbar';
import Footer2 from '../../components/footer2';

const Dashboard = () => {
  return (
    <div
    
    >
      <DashboardNavbar />
      <center>
      <h1>Dashboard</h1>
      </center>
      <Footer2 />
    </div>
  );
};

export default withRouter(Dashboard);
